body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: rgba(203, 239, 241, 0.658);
  color: black;
  padding: 70px;
  font-family: Sans-Serif;
  text-align: center;
}

label {
  align-content: start;
  display: block;
  margin-top: 1rem;
  justify-items: left;
}

label::before{
  display: inline-block;
    margin-right: 4px;
    color: #ff4d4f;
    font-size: 13px;
    font-family: SimSun, sans-serif;
    line-height: 1;
    content: "*";
}

.frame-details {
  border-color: 5px #00000038;
  padding: 70px 40px 40px 70px;
  display: block;
  flex-flow: row wrap;
  width: 30%;
  background-color: white;
  margin: 20;
  text-align: start;
  box-shadow:
    inset 2px 2px 3px rgb(255 255 255 / 60%),
    inset -2px -2px 3px rgb(0 0 0 / 60%);
}

.inner-frame{
  display:block;
  margin: 20;
  padding: 50;
}

.submitButton {
  margin-top: 30px;
  margin-bottom: 10%;
  margin-right: 0;
  width: 50%;
  border: 0;
  line-height: 2.5;
  padding: 0px 20px;
  font-size: 1rem;
  text-align: center;
  align-tracks: flex-end;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  border-radius: 0px;
  background-color: rgb(220, 0, 121);
  background-image: linear-gradient(to top left,
      rgb(0 0 0 / 20%),
      rgb(0 0 0 / 20%) 30%,
      rgb(0 0 0 / 0%));
  box-shadow:
    inset 2px 2px 3px rgb(255 255 255 / 60%),
    inset -2px -2px 3px rgb(0 0 0 / 60%);
}

.submitButton:hover {
  background-color: rgb(255, 0, 140);
}

.submitButton:active {
  box-shadow:
    inset -2px -2px 3px rgb(255 255 255 / 60%),
    inset 2px 2px 3px rgb(0 0 0 / 60%);
}

span {
  color: #e74c3c;
  font-size: 14px;
  margin-top: 5px;
  display: block;
}